/* src/components/Home.css */
.animated-link {
  color: #007BFF;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.animated-link:hover {
  color: #0056b3;
  text-decoration: underline;
}

.script-list {
  list-style-type: none;
  padding: 0;
}

.script-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.script-item a {
  margin-right: 20px;
}

canvas {
  border: 1px solid black;
}
